* {
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 1em;
  overflow: hidden;
}

li{
  display: inline;
  padding: 20px;
}

li a{
  color: #000;
  padding: 8px;
  text-decoration: none;
}

li a:hover{
  border-bottom: 3px black solid;
}


/* TEXT EFFECT TYPE WRITER */
p, h1{
  letter-spacing: 2px;
  line-height:1.9em;
}

.home-content {
  padding: 2%;
  background-color: rgb(255, 255, 255);
  border-right: 3px rgb(228, 227, 227) solid;
  box-shadow: 8px 5px  rgb(202, 200, 200);

}

.split {
  width: 100%;
  height: 100px;
}

.pic-content {
  background-color: rgb(255, 255, 255);
  border-right:3px rgb(228, 227, 227) solid;
  border-bottom:3px rgb(228, 227, 227) solid;
  box-shadow: 8px 5px  rgb(202, 200, 200);
}

#avatar {
  border: 3px rgb(228, 227, 227) solid;
}

/* PILLS */
.techs {
  padding:10px;
  text-transform: uppercase;
  letter-spacing: 2px;
}

.techs-row{
  padding:2%;
}

.badge {
  margin:6px;
  padding:2%;
}

/* CONTACT PAGE */
.contact {
  padding: 2%;
  background-color: rgb(255, 255, 255);
}

/* ABOUT PAGE */
.about {
  padding: 2%;
  background-color: rgb(255, 255, 255);
}

/* PROJECT PAGE */
.project {
  padding: 2%;
  background-color: rgb(255, 255, 255);
}

.cons {
  display: inline-block;
}

#construction {
  position: relative;
  color:steelblue;
  animation: construct 5s infinite;
}

@keyframes construct {
  0%,
  100% {
    left:0px;
  }

  50% {
    left:300px;
  }
}